.App{
  max-width: 1001px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
  /* overflow: hidden; */
}
.buttons-box{
  display: grid;
  flex-direction: column;
  gap: 10px;
}
.buttons-box__container{
  display: flex;
  justify-content: center;
  gap: 10px;
  transition: 0.3s;
}
.button-style{
  transition: 0.3s;
  background-color: whitesmoke;
  text-align: center;
  
}
.button-style:hover{
  background-color: lawngreen;
  transition: 0.3s;
}
.button-style-active{
  transition: 0.3s;
  background-color: green;
}